<template>
  <div class="kuc-input-checkbox">
    <span
      v-for="(item, index) in items"
      :key="index"
      class="kuc-input-checkbox-item"
    >
      <input
        :name="item.name"
        :id="id(index)"
        :disabled="disabled === true ? disabled : item.disabled"
        type="checkbox"
        :checked="isSelected(item)"
        @input="onChange(item)"
      />
      <label :for="id(index)">{{ item.label || '' }} </label>
    </span>
  </div>
</template>

<script>
export default {
  props: {
    items: {
      type: Array,
      default: () => [],
      required: true
    },
    value: {
      type: Array,
      default: () => [],
      required: false
    },
    disabled: {
      type: Boolean,
      default: false,
      required: false
    }
  },

  data() {
    return {
      idPrefix:
        new Date().getTime() +
        '-' +
        this.generateGUID() +
        '-' +
        this.generateGUID() +
        this.generateGUID()
    };
  },

  methods: {
    generateGUID() {
      return Math.floor((1 + Math.random()) * 0x10000)
        .toString(16)
        .substring(1);
    },

    isSelected(item) {
      return this.value
        ? this.value.some(value => value === item.value)
        : false;
    },

    id(index) {
      return this.idPrefix + index;
    },

    onChange(item) {
      const itemValue = item.value;
      const value = this.value ? this.value.slice() : [];
      const length = value.length;
      let include = false;
      for (let i = 0; i < length; i++) {
        if (value[i] === itemValue) {
          include = true;
          value.splice(i, 1);
          break;
        }
      }
      if (!include) {
        value.push(itemValue);
      }
      this.$emit('input', value);
    }
  }
};
</script>

<style scoped>
.kuc-input-checkbox-item {
  display: block;
  margin-right: 16px;
  margin-bottom: 8px;
  padding-left: 1px;
  max-width: 98%;
}
.kuc-input-checkbox-item-block {
  display: block;
}
.kuc-input-checkbox-item-inline {
  display: inline-block;
}
.kuc-input-checkbox-item:hover + label {
  color: #666;
}
.kuc-input-checkbox-item input[type='checkbox'] {
  display: none;
  cursor: pointer;
}
.kuc-input-checkbox-item input[type='checkbox'] + label {
  position: relative;
  display: inline-block;
  margin-left: 32px;
  vertical-align: middle;
  font-size: 14px;
  cursor: pointer;
}
.kuc-input-checkbox-item input[type='checkbox'][disabled] + label {
  color: #bababa;
  cursor: not-allowed;
}
.kuc-input-checkbox-item input[type='checkbox'] + label:before {
  position: absolute;
  top: 50%;
  left: -30px;
  display: inline-block;
  box-sizing: border-box;
  margin-top: -11px;
  width: 21px;
  height: 21px;
  background: #fff;
  background: url(data:image/png;base64,)
    no-repeat center center #fff;
  content: '';
}
.kuc-input-checkbox-item input[type='checkbox']:checked + label:after {
  position: absolute;
  top: 50%;
  left: -30px;
  box-sizing: border-box;
  margin-top: -11px;
  width: 21px;
  height: 21px;
  background: url(data:image/png;base64,)
    no-repeat center center #fff;
  content: '';
}
.kuc-input-checkbox-item
  input[type='checkbox'][disabled]:checked
  + label:after {
  background-image: url(data:image/png;base64,);
}
.kuc-input-radio-item input[type='checkbox'][disabled] + label {
  color: #bababa;
  cursor: not-allowed;
}
</style>
